@media (max-device-width: 320px) {
    h1 {
        font-size: 1.8em;
    }
}
@media (max-device-width: 370px) {
    h1 {
        font-size: 2.2em;
    }
}
@media (min-device-width: 370px) {
    h1 {
        font-size: 2.2em;
    }
}
@media (min-device-width: 900px) {
    h1 {
        font-size: 5.5em;
    }
}
body { padding:2em; font : 100%/1.4 'Helvetica Neue', arial, helvetica, helve, sans-serif; }
